* {
  font-family: Nunito Sans;
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: black;
}

.App {
  padding: 0vh 10vw;
  min-height: 100vh;
  background-color: rgb(247, 247, 247);
  padding-bottom: 2cm;
}

// Home Page Starts Here

.home-box {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  // border: 1px solid black;
}

.content-home-box {
  // border: 1px solid black;
  width: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: .5cm;
}

.home-name {

  font-size: 100px;
  font-weight: bold;
  font-family: Product Sans Regular;
  letter-spacing: .1cm;


}

.n {
  color: #4385f5;
}

.i {
  color: #eb4235;
}

.t {
  color: #fabd05;
}

.e {
  color: #4385f5;

  transform: rotate(-15deg);
  /* Adjust the angle as needed */
  display: inline-block;
  /* Ensures the rotation doesn't affect the layout */
}

.s {
  color: #34a853;
}

.h {
  color: #eb4235;
}

.welcome-box {

  padding: .2cm .3cm;
  display: flex;
  align-items: center;
  margin-top: -.5cm;
  gap: .2cm;
  width: 12cm;
  border: 1px solid rgb(179, 179, 179);
  border-radius: 20px;

}

.welcome {
  font-size: 16px;
  border: none;
  background-color: transparent;
}


.wave {
  width: .7cm;
  height: auto;
  transform: rotate(10deg);

}

.getStarted {
  padding: .2cm .5cm;
  font-size: 16px;
  border-radius: 20px;
  border: 1px solid rgb(196, 196, 196);
  background-color: rgb(255, 255, 255);
  transition: .3s all ease;
  cursor: pointer;

  &:hover {
    background-color: rgb(233, 233, 233);
  }
}

input:focus {
  outline: transparent;
  border: 1px solid rgb(179, 179, 179);

}

.welcome:focus {
  outline: none;
  border: none;
}




// Home Page Ends Here


// Navigation Bar Starts Here

.nav-box {
  padding: .5cm 0cm;

}

.menu {
  display: flex;
  align-items: center;
  gap: .5cm;
}

.nav-name {
  font-size: 40px;
  letter-spacing: 0.04cm;
}

.nav-inp {
  width: 17cm;
  padding: .2cm .3cm;
  border: 1px solid rgb(179, 179, 179);
  background-color: transparent;
  border-radius: 20px;
  font-size: 16px;
}

.menu-list {
  display: flex;
  align-items: center;
  gap: .3cm;
  margin-left: 4cm;
}

.nav-box {
  display: flex;
  flex-direction: column;
  gap: .2cm;
}

.menuItem {
  font-weight: 600;
  font-size: 16px;
  border-bottom: 2.5px solid transparent;
  transition: .3s all ease;
  cursor: pointer;

  &:hover {
    border-bottom: 2.5px solid royalblue;
  }
}

.activeItem {
  border-bottom: 2.5px solid royalblue;
}


.Portfolio {
  display: flex;
  align-items: flex-start;
  gap: .5cm;
  // padding: 0cm .3cm;
  // border: 1px solid black;

}

.portfolio-box {
  // border: 1px solid black;
  width: 17.3cm;
  margin-left: 4cm;
  margin-top: .5cm;
  display: flex;
  flex-direction: column;
  gap: .5cm;
}

.link-head {
  font-size: 22px;
  font-weight: 600;
  color: rgb(26, 79, 238);
  width: fit-content;
  transition: .3s all ease;
  cursor: pointer;
  border-bottom: 2px solid transparent;

  &:hover {
    border-bottom: 2px solid rgb(26, 79, 238);
  }
}

.link-dir {
  font-size: 16px;
  color: rgb(7, 117, 7);
}

.link-dets {
  font-size: 15px;
  color: rgb(87, 87, 87);
}

.contact-box-portfolio {
  margin-top: .5cm;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  // border: 1px solid black;
  padding: .2cm .3cm .3cm .3cm;
  border-radius: 8px;
}

.ch {
  color: black;
  cursor: default;


  &:hover {
    border-bottom: 2px solid transparent;
  }
}

.contact-me {
  border: none;
  padding: .2cm .6cm;
  margin-top: .5cm;
  background-color: rgb(26, 79, 238);
  color: white;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: all .3s ease;

  &:hover {
    background-color: rgb(30, 71, 194);
  }
}

.image-box {
  // border: 1px solid black;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;

  width: 100%;
  flex: 1;
}

.ppic {
  width: 100%;
  height: auto;
}